import { useNear } from "lib/near";
import { useNearWallet } from "lib/near/near-wallet";
import { usePayment } from "models/payment";
import { useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";

export const useModel = () => {
  const near = useNear();
  const location = useLocation();
  const { payment = null, paymentId } = usePayment();
  const nearWallet = useNearWallet({ payment, near });
  const navigate = useNavigate();

  useEffect(() => {
    if (payment == null || !near.isReady) {
      return;
    }

    if (!near.isAuthorized) {
      navigate(`/payment/${paymentId}`);
      return;
    }

    const token = new URLSearchParams(location.search).get("token");
    const currency = payment?.addresses.find((cur) => cur.token === token);
    nearWallet(currency ?? payment?.addresses[0]);
  }, [payment, near, paymentId, navigate, location, nearWallet]);
};
